import React, { Component, Fragment } from "react"
import { Redirect } from "react-router-dom"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { faCheck, faTimes, faEdit } from '@fortawesome/free-solid-svg-icons'
class BotoneraNuevoPedido extends Component {
    state = {
        redirect: false,
        porcentaje: false,
        restar: false,
    }
    porcentaje = () => {
        confirmAlert({
            customUI: ({ onClose }) => {
                return (
                    <div className='custom-ui'>
                        <div className="container">
                            <div className="row">
                                <div className="col-12 form-group text-center">
                                    <h1>Ingresar Porcentaje</h1>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col form-group text-center">
                                    <input type="number" id="porcentaje" className="form-control" placeholder="Porcentaje" />
                                </div>
                            </div>
                            <div className="row">
                                <div className="col form-group-text-cen">
                                    <button type="button" className="btn btn-primary" onClick={e => {
                                        const porcentaje = document.getElementById("porcentaje").value
                                        const nuevoTotal = Math.floor(this.props.total - (this.props.total * porcentaje / 100))
                                        this.props.modificarTotal(nuevoTotal)
                                        onClose()
                                    }}>
                                        OK!
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
        });
    }
    restar = () => {
        confirmAlert({
            customUI: ({ onClose }) => {
                return (
                    <div className='custom-ui'>
                        <div className="container">
                            <div className="row">
                                <div className="col-12 form-group text-center">
                                    <h1>Ingresar Cantidad</h1>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col form-group text-center">
                                    <input type="number" className="form-control" id="cantidad" placeholder="cantidad" />
                                </div>
                            </div>
                            <div class="row">
                                <div class="col form-group text-center">
                                    <button type="button" className="btn btn-primary" onClick={e => {
                                        const cantidad = document.getElementById("cantidad").value
                                        const nuevoTotal = Math.floor(this.props.total - cantidad)
                                        console.log(cantidad)
                                        this.props.modificarTotal(nuevoTotal)
                                        onClose()
                                    }}>
                                        OK!
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
        });
    }
    modificarTotal() {
        confirmAlert({
            customUI: ({ onClose }) => {
                return (
                    <div className='custom-ui'>
                        <div className="container">
                            <div className="row">
                                <div className="col-12  text-center form-group">
                                    <h1>Modificar Total</h1>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col form-group text-center">
                                    <button type="button" className="btn btn-outline-primary" onClick={e => {
                                        onClose()
                                        this.porcentaje()
                                    }}>
                                        Porcentaje
                                    </button>
                                </div>
                                <div className="col form-group text-center">
                                    <button type="button" className="btn btn-outline-primary" onClick={e => {
                                        onClose()
                                        this.restar()
                                    }}>
                                        Restar
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }
        });
    }
    cancelarPedido() {
        confirmAlert({
            title: 'Cancelar Pedido?',
            buttons: [
                {
                    label: 'Si, Cancelar',
                    onClick: async () => {
                        this.setState({
                            redirect: true
                        })
                    }
                },
                {
                    label: 'No'
                }
            ]
        });
    }
    render() {
        return (
            <div className="row align-items-center">
                {this.state.redirect ?
                    <Redirect to="productos" />
                    :
                    null
                }
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light" onClick={this.props.enviarPedido}><FontAwesomeIcon icon={faCheck}  /></button>
                </div>
                <div className="col text-center">
                    <h3 onClick={e => {
                        this.modificarTotal()
                    }}>${this.props.total}</h3>
                </div>
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light" onClick={e => {
                        this.cancelarPedido()
                    }}><FontAwesomeIcon icon={faTimes} /></button>
                </div>
            </div>
        )
    }
}
export default BotoneraNuevoPedido