const reducer = (state, action) => {
    switch (action.type) {
        case "GET_DATA":
            switch (action.section) {
                case 'productos':
                    return {
                        ...state,
                        productos: action.payload
                    }
                    break;
                case 'pedidos':
                    return {
                        ...state,
                        pedidos: action.payload
                    }
                    break;
                case 'clientes':
                    return {
                        ...state,
                        clientes: action.payload
                    }
                    break;
                case 'proveedores':
                    return {
                        ...state,
                        proveedores: action.payload
                    }
                    break;
                case 'deudas':
                    return {
                        ...state,
                        deudas: action.payload
                    }
                    break;
                case 'cheques':
                    return {
                        ...state,
                        cheques: action.payload
                    }
                    break;
                default:
                    break;
            }
            break;
        case "GET_DOCUMENTOS":
            return {
                ...state,
                documentos: action.payload
            }
            break;
        case "RECUPERANDO_USUARIO":
            return {
                ...state,
                recuperarUsuario: action.payload
            }
            break;
        case "IS_LOADING":
            return {
                ...state,
                loading: action.payload
            }
            break;
        case "CHANGE_USER":
            return {
                ...state,
                user: action.payload
            }
            break;
        case "CHANGE_REGISTRO":
            return {
                ...state,
                registro: action.payload
            }
            break;
        case "CHANGE_CLIENTE":
            return {
                ...state,
                cliente: action.payload
            }
            break;
        case "CHANGE_PROVEEDOR":
            return {
                ...state,
                proveedor: action.payload
            }
            break;
        case "CHANGE_EXPRESO":
            return {
                ...state,
                expreso: action.payload
            }
            break;
        default:
            return state
            break;
    }
}
export default reducer