import React, { Component, Fragment } from "react"
import { Link, Redirect } from "react-router-dom"
import withFirebaseAuth from "react-with-firebase-auth"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase/app"
import Logo from "../images/logo.png"
import "./styles/Login.css"
import {
    changeUser,
    changeRegistro,
    isLoading,
    recuperandoUsuario
} from "../actions/index"
import { connect } from "react-redux"

class Login extends Component {
    state = {
        mail: undefined,
        password: undefined
    }
    async logIn() {
        this.props.isLoading(true)
        await firebase.auth().signInWithEmailAndPassword(this.state.mail, this.state.password).then(success => {
            this.props.isLoading(false)
        })
            .catch(error => {
                alert("El usuario o la contrasea son incorrectas")
                this.props.isLoading(false)
            })

    }
    updateMail(data) {
        this.setState(
            {
                mail: data
            }
        )
    }
    updatePass(data) {
        this.setState(
            {
                password: data
            }
        )
    }
    render() {
        return (
            <div>
                {this.props.user ?
                    <Redirect to={"/productos"} />
                    :
                    <div className="container-fluid login">
                        {this.state.loading ?
                            <div className="row">
                                {this.state.loading ? <SpinnerLoading /> : null}
                            </div>
                            :
                            <Fragment>
                                <div className="row">
                                    <div className="col-10 offset-1 text-center">
                                        <img height="125px" src={Logo} />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-10 offset-1 col-md-8 offset-md-2 col-lg-6 offset-lg-3">
                                        <form>
                                            <div className="form-row">
                                                <div className="form-group col-12">
                                                    <label>Email</label>
                                                    <input value={this.state.mail} type="email" onChange={e => {
                                                        this.updateMail(e.target.value)
                                                    }} className="form-control" id="inputEmail" placeholder="Email" />
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col-12">
                                                    <label>Password</label>
                                                    <input value={this.state.password} type="password" onChange={e => {
                                                        this.updatePass(e.target.value)
                                                    }} className="form-control" id="inputPassword4" placeholder="Password" />
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col text-center">
                                                    <Link to="/Productos">
                                                        <button type="submit" onClick={e => {
                                                            e.preventDefault()
                                                            this.logIn()
                                                        }} className="btn btn-success">Ingresar Ahora!</button>
                                                    </Link>
                                                </div>
                                                <div className="form-group col text-center">
                                                    <Link to="/Registro">
                                                        <button type="button" className="btn btn-outline-success" onClick={()=>{
                                                            this.props.changeRegistro(true)
                                                        }}>Crear Una Cuenta!</button>
                                                    </Link>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </Fragment>
                        }
                    </div>
                }
            </div>
        )
    }
}
const mapStateToProps = state => {
    return {
        user: state.user,
        loading: state.loading,
    }
}
const mapDispatchToProps = {
    changeUser,
    changeRegistro,
    isLoading,
    recuperandoUsuario
}
export default connect(mapStateToProps, mapDispatchToProps)(Login)