import React, { Component } from "react"
import * as firebase from "firebase"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import FormNuevoCheque from "../components/FormNuevoCheque"
import { connect } from "react-redux"
class NuevoCheque extends Component {
    render() {
        try {
            const user = this.props.user.email
            if (user) {
                return (
                    <div>
                        <FormNuevoCheque />
                    </div>
                )
            }
        }
        catch{
            return (
                <Login redirect="NuevoCheque" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user
})
export default connect(mapStateToProps, null)(NuevoCheque)