import React, { Component } from "react"
import Product from "../components/Product"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import { connect } from "react-redux"

class NoMecanizadosList extends Component {
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayProductos = document.getElementsByClassName("col-12 col-md-6 col-lg-4 form-group text-center align-items-center")
        forEach.call(arrayProductos, function (f) {
            if (f.children[0].children[0].children[1].children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-12 col-md-6 col-lg-4 form-group text-center d-none align-items-center"
                console.log(f.className)
            }
            else {
                f.className = "col-12 col-md-6 col-lg-4 form-group text-center d-flex align-items-center"
            }
        });
    }
    render() {
        try {
            let user
            user = this.props.user.email
            if (user) {
                return (
                    <div>
                        <div className="productList">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-6 offset-3">
                                        <form>
                                            <div className="formRow">
                                                <div className="form-group col-12 text-center">
                                                    <input type="text" className="form-control" onChange={
                                                        e => {
                                                            this.handleChange(e)
                                                        }
                                                    } placeholder="Buscar Producto" />
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                                <div className="row">
                                    {Object.values(this.props.noMecanizados).map(producto => (
                                        <div className="col-12 col-md-6 col-lg-3 form-group text-center d-flex align-items-center" key={producto.nombre}>
                                            <Product nombre={producto.nombre} precio={producto.precio} cantidad={producto.cantidad} imagen={this.props.images[producto.nombre]} seccion="no-mecanizados" />
                                        </div>
                                    ))
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                )
            }
        }
        catch (error) {
            return (
                <Login redirect="productos" />
            )
        }
    }
}
const mapStateToProps = state => {
    return {
        loading: state.loading,
        products: state.data["productos2"],
        mecanizados: state.data["mecanizados2"],
        noMecanizados: state.data["no-mecanizados2"],
        images: state.images,
        user: state.user
    }
}
export default connect(mapStateToProps, null)(NoMecanizadosList)