import React, { Component } from "react"
import Login from "../components/Login"
import Product from "../components/Product"
import { confirmAlert } from 'react-confirm-alert'; // Import
import { connect } from "react-redux"
import * as firebase from "firebase"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFrownOpen,faPlus} from '@fortawesome/free-solid-svg-icons'
import "./Styles/ProductList.css"

class ProductList extends Component {
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayProductos = document.getElementsByClassName("col-12 col-md-6 col-lg-3 form-group text-center align-items-center")
        forEach.call(arrayProductos, function (f) {
            if (f.children[0].children[0].children[0].children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-12 col-md-6 col-lg-3 form-group text-center d-none align-items-center"
            }
            else {
                f.className = "col-12 col-md-6 col-lg-3 form-group text-center d-flex align-items-center"
            }
        });
    }

    alertAgregarProducto=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nuevo Producto</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" className="form-control" id='nombreNuevoProducto' placeholder="Nombre del Producto"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input type="text" className="form-control" id='precioNuevoProducto' placeholder="Precio"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarProducto()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarProducto = async () =>{
        const nombre = document.getElementById('nombreNuevoProducto').value
        const precio = document.getElementById('precioNuevoProducto').value
        if(nombre){
            await firebase.database().ref().child(this.props.user.uid).child('productos').update(
                {
                    [nombre]:{
                        nombre:nombre,
                        cantidad:0,
                        precio:precio
                    }
                }
            )
        }
    }
    alertGuia = () =>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon questionIcon" icon={faFrownOpen}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>No hay productos</h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <p> Ingrese el nombre y precio de un producto para poder comenzar a utilizar el Control De Stock
                                </p>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" className="form-control" id='nombreNuevoProducto' placeholder="Nombre del Producto"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input type="text" className="form-control" id='precioNuevoProducto' placeholder="Precio"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarProducto()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    render() {
        console.log(this.props.productos)
        try {
            let user
            user = this.props.user.email
            if (user) {
                return (
                    <div>
                        <div className="productList">
                            <div className="container-fluid">
                                {!this.props.productos?
                                <>
                                    {this.alertGuia()}
                                    <div className="row">
                                        <div className="form-group col-12 text-center">
                                            <h1 className='text-white'>Aun no hay ningun producto!</h1>
                                        </div>
                                        <div className="form-group col-12 text-center">
                                        <button type="button" className="btn btn-success btnProduct" onClick={this.alertAgregarProducto}>
                                            Agregar nuevo Producto!
                                        </button>
                                        </div>
                                    </div>
                                </>
                                :
                                <>
                                    <div className="row align-middle">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Producto" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                        <div className="col">
                                            <button type="button" className="btn btn-success btnProduct" onClick={this.alertAgregarProducto}>
                                                Agregar nuevo Producto!
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.values(this.props.productos).map(producto => (
                                            <div className="col-12 col-md-6 col-lg-3 form-group text-center d-flex align-items-center" key={producto.nombre}>
                                                {console.log(producto)}
                                                <Product nombre={producto.nombre} precio={producto.precio} cantidad={producto.cantidad} seccion="productos" />
                                            </div>
                                        ))
                                        }
                                    </div>
                                </>
                                }
                            </div>
                        </div>
                    </div>
                )
            }
        }
        catch (error) {
            return (
                <Login redirect="productos" />
            )
        }
    }
}
const mapStateToProps = state => {
    return {
        loading: state.loading,
        productos: state.productos,
        user: state.user
    }
}
export default connect(mapStateToProps, null)(ProductList)