import React, { Component } from "react"
import Pedido from "../components/Pedido"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import "./Styles/PedidosList.css"
import * as firebase from "firebase"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import projectManagement from "../images/projectManagement.jpg"
import { connect } from "react-redux"

class PedidosList extends Component {

    async enviarPedido(key, pedido,uid) {
        const productosRef = await firebase.database().ref().child(uid).child('clientes').child(pedido.cliente)
        if (productosRef.child("pedidos")) {
            let id = 0;
            const date = new Date()
            console.log(`${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`)
            productosRef.child("pedidos").push({
                fecha: `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`,
                articulos: pedido.articulos
            })
            this.eliminarPedido(key)
        }
    }
    async eliminarPedido(key) {
        await firebase.database().ref().child(this.props.user.uid).child('pedidos').child(key).remove()
    }
    render() {
        console.log(this.props.user.uid)
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.pedidos) {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        {Object.keys(this.props.pedidos).map(key => (
                                            <div className="col-12 col-md-6 col-lg-4 form-group text-center d-flex align-items-center">
                                                <Pedido id={key} pedido={this.props.pedidos[key]} cliente={this.props.pedidos[key].cliente} enviarPedido={(key,pedido)=>{
                                                    this.enviarPedido(key,pedido,this.props.user.uid)
                                                }} eliminarPedido={this.eliminarPedido} />
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>

                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-12 form-group text-center">
                                            <h3 className="shadowTitle">Aun No Hay Ningun Pedido!</h3>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch{
            return (
                <Login redirect="pedidos" />
            )
        }
    }
}
const mapStateToProps = state => ({
        pedidos: state.pedidos,
        user: state.user
})
export default connect(mapStateToProps, null)(PedidosList)
