import React, { Component, Fragment } from "react"
import { Redirect } from "react-router-dom"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { faCheck, faTimes, faEdit } from '@fortawesome/free-solid-svg-icons'
class BotoneraPedido extends Component {
    render() {
        return (
            <div className="row">
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light" onClick={this.props.enviarPedido}><FontAwesomeIcon icon={faCheck} /></button>
                </div>
                <div className="col text-center">
                    <span className="badge badge-light">
                        <h4>$ {this.props.precio}</h4>
                    </span>
                </div>
                <div className="col text-center">
                    <button type="button" className="btn btn-outline-light" onClick={this.props.eliminarPedido}><FontAwesomeIcon icon={faTimes}  /></button>
                </div>
            </div>
        )
    }
}
export default BotoneraPedido