import React, { Component } from "react"
import PedidoCliente from "./PedidoCliente"
import PedidoProovedor from "./PedidoProovedor"


class Fichapedido extends Component {
    render() {
        if (this.props.data !== undefined) {
            console.log(this.props.data)
            let pedidosAux = []
            Object.keys(this.props.data).map(pedido => {
                console.log(pedido)
                pedidosAux.push(this.props.data[pedido])
            })
            return (
                <div className="container">
                    <div className="row">
                        {pedidosAux.reverse().map(pedido => (
                            <div className="col-12 col-md-6 col-lg-4 form-group d-flex align-items-center">
                                {this.props.seccion === "proveedores" ?
                                    <PedidoProovedor data={pedido} />
                                    :
                                    <PedidoCliente data={pedido} />
                                }
                            </div>
                        ))}
                    </div>
                </div>
            )
        }
        else {
            return (
                null
            )
        }
    }
}
export default Fichapedido