import React, { Component } from "react"
import { Link } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import FichaProovedor from "../components/FichaProovedor"
import "./Styles/ProovedoresList.css"
import { connect } from "react-redux"
import { changeExpreso } from "../actions/index"


class ExpresosList extends Component {
    returnBack() {
        this.props.changeExpreso(null)

    }
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        console.log(arrayClientes)
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.parentNode.style.display = "none";
            else
                f.parentNode.parentNode.style.display = "block";
        });
    }
    render() {
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.expreso) {
                    const expreso = this.props.expresos[this.props.expreso]
                    return (
                        <div>
                            <FichaProovedor seccion="expresos" handleClick={e => {
                                this.returnBack()
                            }} proovedor={expreso} />
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Expreso" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.values(this.props.expresos).map(expreso => (
                                            <div className="col-12 col-md-6 col-lg-4 text-center form-group">
                                                <Link to={{
                                                    pathname: `/expresos/`,
                                                    search: `${expreso.datos.nombre}`
                                                }}>
                                                    <button type="button" className="btn btn-outline-light btn-cliente" onClick={() => {
                                                        this.props.changeExpreso(expreso.datos.nombre)
                                                    }}>
                                                        {expreso.datos.nombre}
                                                    </button>
                                                </Link>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch{
            return (
                <Login redirect="Expresos" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    expresos: state.data["expresos2"],
    expreso: state.expreso
})
const mapDispatchToProps = {
    changeExpreso,

}
export default connect(mapStateToProps, mapDispatchToProps)(ExpresosList)