import React, { Component, Fragment } from "react"
import BotoneraNuevoPedido from "../components/BotoneraNuevoPedido"
import './styles/ResumenPedido.css'
class ResumenPedido extends Component {
    render() {
        return (
        <div className="col bg-dark text-center nuevoPedido p-2 ">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h4>{this.props.cliente}</h4>
                    </div>
                </div>
                    {Object.keys(this.props.pedido).map(key => (
                                <div className="row align-items-center">
                                    <div className="col d-flex">
                                        <p className='flex-grow-1 text-left'>{this.props.pedido[key].nombre} <span className="badge badge-light">$ {this.props.pedido[key].precio} c/u </span></p>
                                        <p className='mr-2 ml-2'>{this.props.pedido[key].cantidad}</p>
                                    </div>
                                </div>
                    )
                    )}
                <BotoneraNuevoPedido modificarTotal={total => {
                    this.props.modificarTotal(total)
                }} total={this.props.total} enviarPedido={this.props.enviarPedido} />
            </div>
        </div>
        )
    }
}
export default ResumenPedido