import React, { Component, Fragment } from "react"
import Item from "./Item"
import "./styles/ficha.css"
import * as firebase from 'firebase'
import {connect} from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faPlus, faMinus,faQuestionCircle } from '@fortawesome/free-solid-svg-icons'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { changeCliente,changeProveedor } from "../actions/index"

class Ficha extends Component {
    alertModificarDeuda=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Modificar Deuda</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="number" className="form-control" id='inputNuevaDeuda' placeholder="Inresar Cantidad"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.sumarDeuda()
                                            onClose();
                                        }}
                                    >
                                    <FontAwesomeIcon icon={faPlus}  />
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-danger'
                                        onClick={() => {
                                            this.restarDeuda()
                                            onClose();
                                        }}
                                    >
                                        <FontAwesomeIcon icon={faMinus}  />
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarValor=async (key)=>{
        const nuevaInfo = document.getElementById('inputNuevaInfo').value
        let newArr = this.props.cliente.datos.info[key]
        if(nuevaInfo){
            newArr.push(nuevaInfo)
            const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.cliente.datos.nombre).child("datos").child('info')
            await datosRef.update({
                [key]:newArr
            }
            )
            this.props.editarCliente()
        }
    }
    alertAgregarValor=(key)=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar {key}</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" className="form-control" id='inputNuevaInfo' placeholder="Ingresar Informacion..."/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarValor(key)
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarCampo=async ()=>{
        const nuevoCampo = document.getElementById('inputNuevoCampo').value
        const nuevoValor = document.getElementById('inputNuevoValor').value
        if(nuevoCampo){
            const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.cliente.datos.nombre).child("datos").child('info')
            await datosRef.update({
                [nuevoCampo]:{
                    0:nuevoValor
                }
            }
            )
            this.props.editarCliente()
        }
    }
    alertAgregarCampo=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nueva Informacion</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <div className="input-group">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text" id="inputGroup-sizing-default">Campo</span>
                                        </div>
                                        <input autoFocus type="text" className="form-control" id='inputNuevoCampo' placeholder="ej: Ciudad"/>
                                    </div>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group">
                                    <div className="input-group">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text" id="inputGroup-sizing-default">Valor</span>
                                        </div>
                                        <input type="text" className="form-control" id='inputNuevoValor' placeholder="ej: Buenos Aires"/>
                                    </div>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarCampo()
                                            onClose();
                                        }}
                                    >
                                    <FontAwesomeIcon icon={faPlus}  />
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-danger'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        <FontAwesomeIcon icon={faMinus}  />
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    eliminarValor=async (key,valores,valorEliminado)=>{
        const result = valores.filter(valor => valor!==valorEliminado);
        const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.cliente.datos.nombre).child("datos").child('info')
        await datosRef.update({
                [key]:result
        }
        )
        this.props.editarCliente()
        
    }
    alertEliminarValor=(key,valores,valor)=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon questionIcon" icon={faQuestionCircle}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Eliminar Informacion</h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-left">
                                <p>Desea eliminar {valor} ?</p>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.eliminarValor(key,valores,valor)
                                            onClose();
                                        }}
                                    >
                                    Si, Eliminar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    alertEditarNombre=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon questionIcon" icon={faQuestionCircle}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Editar Nombre de {this.props.cliente.datos.nombre}</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" className="form-control" id='inputNuevoNombre' placeholder="Ingresar Nuevo Nombre"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.editarNombre()
                                            onClose();
                                        }}
                                    >
                                    Si, Editar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    editarNombre= async ()=>{
        const nuevoNombre = document.getElementById('inputNuevoNombre').value
        if(nuevoNombre){
            let data = this.props.cliente
            await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(data.datos.nombre).remove()
            data.datos.nombre=nuevoNombre
            const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion)
            await datosRef.update({
                [data.datos.nombre]:data
            }
            )
            if(this.props.seccion==='clientes'){
                this.props.changeCliente(data.datos.nombre)
            }
            else{
                this.props.changeProveedor(data.datos.nombre)
            }
        }
    }
    async sumarDeuda() {
        const nuevaDeuda = parseFloat(document.getElementById('inputNuevaDeuda').value)
        if(nuevaDeuda){
            const deudaRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.cliente.datos.nombre).child("datos")
            await deudaRef.update({
                deuda: parseFloat(this.props.cliente.datos.deuda)+nuevaDeuda
            }
            )
        }
    }
    async restarDeuda() {
        const nuevaDeuda = parseFloat(document.getElementById('inputNuevaDeuda').value)
        if(nuevaDeuda){
            const deudaRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.cliente.datos.nombre).child("datos")
            await deudaRef.update({
                deuda: parseFloat(this.props.cliente.datos.deuda)-nuevaDeuda
            }
            )
        }
    }
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col form-group name">
                        {this.props.edit?
                            <h1 onClick={this.alertEditarNombre} className='edit'>{this.props.cliente.datos.nombre}<span className={this.props.cliente.datos.deuda>0?'badge badge-pill badge-danger ml-4 deuda':'badge badge-pill badge-success ml-4 deuda'} >${this.props.cliente.datos.deuda}</span></h1>
                        :
                            <h1>{this.props.cliente.datos.nombre}<span className={this.props.cliente.datos.deuda>0?'badge badge-pill badge-danger ml-4 deuda':'badge badge-pill badge-success ml-4 deuda'} onClick={this.alertModificarDeuda} >${this.props.cliente.datos.deuda}</span></h1>
                        }
                    </div>
                </div>
                {this.props.edit?
                <div className="row">
                    <div className="col-12 form-group text-center">
                        <button type='button' className="btn btn-success" onClick={this.alertAgregarCampo}>
                            Agregar nuevo campo
                        </button>
                    </div>
                </div>
                :
                null
                }
                <div className="row">
                    {this.props.cliente.datos.info?
                    (
                        <table className="table table-hover table-dark">
                            <tbody>
                                {
                                    Object.keys(this.props.cliente.datos.info).map(key => (
                                    (key === "nombre"||key==='deuda' ?
                                        null
                                        :
                                        (
                                        <tr>
                                            {this.props.edit?
                                            <>
                                                <th scope="row">{key}</th>
                                                <td>
                                                    {this.props.cliente.datos.info[key].map(valor=>( 
                                                        <p className='mt-2 mb-2 eliminarValor' onClick={()=>{
                                                            this.alertEliminarValor(key,this.props.cliente.datos.info[key],valor)
                                                        }}>{valor}</p>
                                                    ))}
                                                </td>
                                                <th>
                                                    <FontAwesomeIcon className='newInfo'icon={faPlus} onClick={()=>{
                                                        this.alertAgregarValor(key)
                                                    }}  />
                                                </th>
                                            </>
                                            :
                                            <>
                                                <th scope="row">{key}</th>
                                                <td>
                                                    {this.props.cliente.datos.info[key].map(valor=>( 
                                                        <p className='mt-2 mb-2'>{valor}</p>
                                                    ))}
                                                </td>
                                            </>
                                            }
                                        </tr>
                                        )
                                    )
                                ))
                                }
                            </tbody>
                        </table>
                    )
                    :
                    <>
                        <div className="col-12 text-center">
                            <h1 className='text-white'> <span className='badge badge-pill bdage-info'> Aun no hay Informacion para mostrar</span></h1>
                        </div>
                        <div className="col-12 text-center">
                            <button type='button' className='btn btn-success' onClick={this.alertAgregarCampo}> Agregar Informacion!</button>
                        </div>
                    </>
                    }
                </div>
            </div>
        )
    }
}
const mapStateToProps=state=>({
    user:state.user
})
const mapDispatchToProps={
    changeCliente,
    changeProveedor
}
export default connect(mapStateToProps,mapDispatchToProps)(Ficha)

