import React, { Component } from "react"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import "./Styles/ChequesList.css"
import { connect } from "react-redux"
import * as firebase from "firebase"

class ListasDePrecios extends Component {
    async subirDocumento() {
        if (this.state.file) {
            console.log(this.state.file)
            await firebase.storage().ref().child("ListasDePrecios").child(this.state.file.name).put(this.state.file).then(e => {
                this.setState({
                    file: undefined,
                    documentos: undefined,
                    loading: true
                })
                this.fetchData()
            })
        }
    }
    async borrarDocumento(documento) {
        confirmAlert({
            title: 'Eliminar Documento?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        await firebase.storage().ref().child("ListasDePrecios").child(documento).delete().then(e => {
                            this.setState({
                                documentos: undefined,
                                edit: false,
                                loading: true
                            })
                            this.fetchData()
                        }
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        console.log(this.props.documentos)
        try {
            const user = this.props.user.email
            if (user) {
                return (
                    <div>
                        <div className="productList">
                            <div className="container">
                                <div className="row">
                                    <div className="col-12  col-md-8 offset-md-2 col-lg-6 offset-lg-3 form-group">
                                        <div class="input-group mb-3">
                                            <div class="input-group-prepend">
                                                {this.state.file ?
                                                    <button type="button" className="btn btn-success" onClick={e => {
                                                        this.subirDocumento()
                                                    }}>Subir Documento!</button>
                                                    :
                                                    <button type="button" className="btn btn-success disabled">Subir Documento!</button>
                                                }
                                            </div>
                                            <div class="custom-file">
                                                <input type="file" class="custom-file-input" id="inputGroupFile01" onChange={e => {
                                                    this.setState(
                                                        {
                                                            file: e.target.files[0]
                                                        }
                                                    )
                                                }} />
                                                <label class="custom-file-label" for="inputGroupFile01">{
                                                    this.state.file ?
                                                        "Archivo Elegido!"
                                                        :
                                                        "Elegir Archivo"
                                                }</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12 form-group text-center">
                                        <button type="button" className="btn btn-danger" onClick={e => {
                                            if (this.state.edit) {
                                                this.setState({
                                                    edit: false
                                                })
                                            }
                                            else {
                                                this.setState({
                                                    edit: true
                                                })
                                            }
                                        }}>Eliminar Archivo</button>
                                    </div>
                                </div>
                                <div className="row">
                                    {Object.keys(this.props.documentos).map(key => (
                                        console.log(this.props.documentos[key].url)
                                            (this.state.edit ?
                                                <div className="col-12 form-group text-center">
                                                    <button type="button" className="btn btn-outline-danger" onClick={e => {
                                                        this.borrarDocumento(this.props.documentos[key].nombre)
                                                    }}>{this.props.documentos[key].nombre.slice(0, this.props.documentos[key].nombre.indexOf("."))}</button>
                                                </div>
                                                :
                                                <div className="col-12 form-group text-center">
                                                    <button type="button" className="btn btn-outline-light" onClick={e => {
                                                        window.open(this.props.documentos[key].url)
                                                    }}>{this.props.documentos[key].nombre.slice(0, this.props.documentos[key].nombre.indexOf("."))}</button>
                                                </div>
                                            )
                                    ))}

                                </div>
                            </div>
                        </div>
                    </div>

                )
            }
        }
        catch{
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    documentos: state.documentos,
})
export default connect(mapStateToProps, null)(ListasDePrecios)