import React, { Component, Fragment } from "react"
import { Redirect } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import ProductElection from "../components/ProductElection"
import * as firebase from "firebase"
import ResumenPedido from "../components/ResumenPedido"
import "./Styles/ProovedoresList.css"
import "./Styles/NuevoPedido.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faQuestionCircle,faPlus} from '@fortawesome/free-solid-svg-icons'
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { connect } from "react-redux"
import {changeProveedor} from "../actions/index"
class NuevoRecibo extends Component {
    seleccionarDestinatario(cliente) {
        this.setState({
            nuevoCliente: cliente
        })
    }
    async enviarPedido() {
        let arr = []
        Object.keys(this.state.pedido).map(key => {

            arr.push(
                {   precio:this.state.pedido[key].precio,
                    nombre: this.state.pedido[key].nombre,
                    cantidad: this.state.pedido[key].cantidad,
                    seccion: this.state.pedido[key].seccion
                }
            )
        })
        const date = new Date()
        firebase.database().ref().child(this.props.user.uid).child('proveedores').child(this.state.nuevoCliente).child("pedidos").push(
            {
                fecha: `${date.getDay()}/${date.getMonth()}/${date.getFullYear()}`,
                articulos: arr
            }
        )
        arr.map(articulo=>{
            const nuevaCantidad=this.props.productos[articulo.nombre].cantidad+=parseFloat(articulo.cantidad)
            firebase.database().ref().child(this.props.user.uid).child('productos').child(articulo.nombre).update(
                {
                    cantidad:nuevaCantidad
                }
            )
        })
    }
    alertEnviarPedido() {
        confirmAlert({
            title: 'Agregar Recibo?',
            buttons: [
                {
                    label: 'Si, Agregar',
                    onClick: async () => {
                        this.enviarPedido().then(
                            this.setState({
                                redirect: true
                            })
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    state = {
        nuevoCliente: undefined,
        accion:undefined,
        total: 0,
        pedido: [],
    }
    agregarArticulo(nombre, cantidad, precio, seccion) {
        const arr = this.state.pedido
        arr.push({
            nombre: nombre,
            cantidad: cantidad,
            precio: precio,
            seccion: seccion
        })
        super.setState({
            ...this.state,
            pedido: arr
        })
    }
    handleChangeProducto(e) {
        var forEach = Array.prototype.forEach;
        var array = document.getele
        var arrayProductos = document.getElementsByClassName("col-6 col-md-3 col-lg-2 form-group")
        forEach.call(arrayProductos, function (f) {
            if (f.children[0].children[0].children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-6 col-md-3 col-lg-2 form-group d-none "
                console.log(f.className)
            }
            else {
                f.className = "col-6 col-md-3 col-lg-2 form-group d-block "
            }
        });
    }
    handleChangeCliente(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.style.display = "none";
            else
                f.parentNode.style.display = "block";
        });
    }
    modificarTotal(total) {
        console.log(total)
        this.setState({ total: total })
    }
    alertNuevoCliente = () =>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nuevo Proveedor</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" class="form-control" id='nombreNuevoCliente' placeholder="Nombre del Cliente"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.nuevoCliente()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
          });
    }
    alertElegirProducto = (nombre) =>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>{nombre}</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="number" class="form-control" id='cantidadProducto' placeholder="Cantidad"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input type="number" class="form-control" id='precioProducto' placeholder="Precio"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            const cantidadPlus = document.getElementById('cantidadProducto').value
                                            const precio = document.getElementById('precioProducto').value
                                                if (cantidadPlus) {
                                                    this.agregarArticulo(nombre,cantidadPlus,precio, "productos")
                                                    if (precio) {
                                                        let sumaPrecios
                                                        if (this.state.total) {
                                                            sumaPrecios = this.state.total
                                                        }
                                                        else {
                                                            sumaPrecios = 0
                                                        }
                                                        sumaPrecios += parseInt(precio * cantidadPlus)
                                                        this.setState({
                                                            total: sumaPrecios
                                                        })
                                                    }
                                                }
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            );
            }
        });
    }
    nuevoCliente = async () =>{
        const nombre = document.getElementById("nombreNuevoCliente").value
        if(nombre){
            this.seleccionarDestinatario(nombre)
            firebase.database().ref().child(this.props.user.uid).child('proveedores').update({
                [nombre]:{
                    datos:{
                        nombre:nombre,
                        deuda:0
                    }
                }
            })
        }
    }
    alertResumen = ()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container">
                        <div className="row">
                            <ResumenPedido
                                cliente={this.state.nuevoCliente}
                                pedido={this.state.pedido}
                                enviarPedido={e => {
                                    this.alertEnviarPedido()
                                }}
                                modificarTotal={total => {
                                    this.modificarTotal(total)
                                }}
                                total={this.state.total}
                            />
                        </div>
                    </div>
                </div>
            );
            }
        });
    }
    alertAgregarProducto=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nuevo Producto</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" className="form-control" id='nombreNuevoProducto' placeholder="Nombre del Producto"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input type="text" className="form-control" id='precioNuevoProducto' placeholder="Precio"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarProducto()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarProducto = async () =>{
        const nombre = document.getElementById('nombreNuevoProducto').value
        const precio = document.getElementById('precioNuevoProducto').value
        if(nombre){
            await firebase.database().ref().child(this.props.user.uid).child('productos').update(
                {
                    [nombre]:{
                        nombre:nombre,
                        cantidad:0,
                        precio:precio
                    }
                }
            )
        }
    }
    render() {
        try {
            let user = this.props.user.email
            if (user) {
                if (!this.state.nuevoCliente) {
                    return (
                        <div className="nuevo-cliente ">
                            <div className="container-fluid mt-4">
                                <div className="row">
                                    <div className="col-6 offset-3">
                                        <form>
                                            <div className="formRow">
                                                <div className="form-group col-12 text-center">
                                                    <input type="text" className="form-control" onChange={
                                                        e => {
                                                            this.handleChangeCliente(e)
                                                        }
                                                    } placeholder="Buscar Proveedor" />
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                    <div className="col">
                                        <button type='button' className='btn btn-outline-light' onClick={this.alertNuevoCliente}>
                                            Nuevo Proveedor
                                        </button>
                                    </div>
                                </div>
                                {this.props.proveedores?
                                <div className="row">
                                    {Object.values(this.props.proveedores).map(cliente => (
                                        <div className="col-12 text-center form-group">
                                            <button type="button" className="btn btn-primary btn-cliente" onClick={() => {
                                                console.log(cliente.datos.nombre)
                                                this.seleccionarDestinatario(cliente.datos.nombre)
                                            }}>
                                                {cliente.datos.nombre}
                                            </button>
                                        </div>
                                    ))}
                                </div>
                                :
                                <div className="row">
                                    <div className="col-12 text-center">
                                        <h1 className='text-white'>Aun no hay Proveedores!</h1>
                                    </div>
                                </div>
                                }
                            </div>
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-12 text-center">
                                            <h1 className="name">Elegir Productos</h1>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChangeProducto(e)
                                                            }
                                                        } placeholder="Buscar Producto" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                        <div className="col">
                                            <button type='button' className='btn btn-success' onClick={this.alertResumen}>
                                                Finalizar Recibo
                                            </button>
                                        </div>
                                        <div className="col text-center">
                                            <button type='button' className='btn btn-outline-success' onClick={this.alertAgregarProducto}>
                                                Nuevo Producto
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row productList align-items-center">
                                        {this.props.productos?
                                            Object.values(this.props.productos).map(producto => ( 
                                                <div className="col-6 col-md-3 col-lg-2 form-group" key={producto.nombre}>
                                                    <ProductElection 
                                                        handleClick={e => {
                                                            this.alertElegirProducto(producto.nombre, producto.precio)
                                                        }}
                                                        nombre={producto.nombre} 
                                                        precio={producto.precio} 
                                                    />
                                                </div>
                                            ))
                                        :
                                        <div className="col-12 text-center">
                                            <h1 className='text-white'> Aun No Hay Productos!</h1>
                                        </div>
                                        }
                                    </div>
                                    {this.state.redirect ?
                                        <Redirect to="productos" />
                                        :
                                        null
                                    }
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch (error) {
            return (
                <Login redirect="NuevoRecibo" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    productos: state.productos,
    proveedores:state.proveedores
})
const mapDispatchToProps={
    changeProveedor
}
export default connect(mapStateToProps, mapDispatchToProps)(NuevoRecibo)