import React, { Component, Fragment } from "react"
import "./styles/PedidoCliente.css"

class PedidoCliente extends Component {
    render() {
        console.log(this.props.data)
        return (
            <div className="container p2 pedido bg-dark">
                <div className="container">
                    <div className="row">
                        <div className="col pt-1 pb-1">
                            <h3>{this.props.data.fecha}</h3>
                        </div>
                    </div>
                </div>
                <div className="container">
                    {this.props.data.articulos.map(articulo => (
                        <div className="row separator d-flex">
                                <div className=" pt-2 pb-2 ">
                                    <p>{articulo.cantidad} </p>
                                </div>
                                <div className=" pt-2 pb-2 text-right flex-grow-1">
                                    <p>{articulo.nombre}</p>
                                </div>
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}
export default PedidoCliente