import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import * as firebase from "firebase"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'


class FormNuevoCheque extends Component {
    state = {
        entrada: null,
        cliente: null,
        banco: null,
        numero: null,
        fecha: null,
        monto: null
    }

    async submitForm() {
        const date = new Date()
        let cheque = {
            Banco: this.state.banco,
            Cliente: this.state.cliente,
            ["Fecha Del Cheque"]: this.state.fecha,
            ["Fecha Del Dia"]: date.getDate() + "/" + (date.getMonth() + 1) + "/" + date.getFullYear(),
            Monto: this.state.monto,
            Numero: this.state.numero,
        }
        await firebase.database().ref().child(this.props.user.uid).child("cheques").push(cheque)
    }
    confirm() {
        confirmAlert({
            title: 'Agregar Cheque?',
            buttons: [
                {
                    label: 'Agregar',
                    onClick: async () => {
                        this.submitForm().then(
                            this.setState({
                                redirect: true
                            })
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        return (
            <div className="container mt-4">
                <div className="row">
                    <div className="col-6 offset-3">
                        <form onSubmit={e => {
                            e.preventDefault()
                            this.confirm()
                        }}>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label>Cliente</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.cliente} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            cliente: e.target.value
                                        })
                                    }} />
                                </div>

                                <div className="form-group col-md-12">
                                    <label for="inputZip">Banco</label>
                                    <input type="text" className="form-control" id="inputZip" value={this.state.banco} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            banco: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label>Numero De Cheque</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.numero} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            numero: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label>Fecha Del Cheque</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.fecha} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            fecha: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-md-12">
                                    <label>Monto</label>
                                    <input type="text" className="form-control" id="inputCity" value={this.state.monto} onChange={e => {
                                        this.setState({
                                            ...this.state,
                                            monto: e.target.value
                                        })
                                    }} />
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="form-group col-12 text-center">
                                    <button type="submit" className="btn btn-success">Agregar Cheque</button>
                                </div>
                            </div>

                        </form>
                        {this.state.redirect ?
                            <Redirect to="/Cheques" />
                            :
                            null
                        }
                    </div>
                </div>
            </div>

        )
    }
}
const mapStateToProps=state=>({
    user:state.user
})
export default connect(mapStateToProps,null)(FormNuevoCheque)