import React, { Component, Fragment } from "react"
import {Redirect} from 'react-router-dom'
import NavBar from "./NavBar"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import Login from "../components/Login"
import Registro from "../components/Registro"
import {
    getProductos,
    getPedidos,
    getClientes,
    getProveedores,
    getDeudas,
    getCheques,
    getDocumentos,
    changeUser,
    isLoading,
    recuperandoUsuario
} from "../actions/index"
import { connect } from "react-redux"


class Layout extends Component {
    
    logOut = async () => {
        await firebase.auth().signOut()
        window.location.reload()
    }
    async fetchData(id) {
        this.props.isLoading(true)
        const productosRef = await firebase.database().ref().child(id).child('productos')
        productosRef.on("value", snapshot => {
            this.props.getProductos(snapshot.val())
        })
        const pedidosRef = await firebase.database().ref().child(id).child('pedidos')
        pedidosRef.on("value", snapshot => {
            this.props.getPedidos(snapshot.val())
        })
        const clientesRef = await firebase.database().ref().child(id).child('clientes')
        clientesRef.on("value", snapshot => {
            this.props.getClientes(snapshot.val())
        })
        const proveedoresRef = await firebase.database().ref().child(id).child('proveedores')
        proveedoresRef.on("value", snapshot => {
            this.props.getProveedores(snapshot.val())
        })
        const deudasRef = await firebase.database().ref().child(id).child('deudas')
        deudasRef.on("value", snapshot => {
            this.props.getDeudas(snapshot.val())
        })
        const chequesRef = await firebase.database().ref().child(id).child('cheques')
        chequesRef.on("value", snapshot => {
            this.props.getCheques(snapshot.val())
            this.props.isLoading(false)
        })
    }
    async authListener() {
        firebase.auth().onAuthStateChanged(async user=>{
            if (user) {
                await this.fetchData(user.uid)
                this.props.changeUser(user)
            }
            this.props.recuperandoUsuario(false)
        })
    }
    async componentDidMount() {
        await this.authListener()
    }
    render() {
        console.log(this.props.user)
        return (
            <>
                {
                    this.props.recuperarUsuario ?
                        <>
                            <SpinnerLoading title="Recuperando Usuario..." />
                        </>
                        :
                        <div className="app">
                            {!this.props.user ?
                                (this.props.registro?
                                <Registro/>
                                :
                                <Login />
                                )
                                :
                                <>
                                    {this.props.loading ?
                                        <SpinnerLoading title="Recuperando Informacion..." />
                                        :
                                        <>
                                            <NavBar user={this.props.user} handleClick={this.logOut} />
                                            {this.props.children}
                                        </>
                                    }
                                </>
                            }
                        </div>
                }
            </>
        )
    }
}
const mapStateToProps = state => ({
    loading: state.loading,
    user: state.user,
    registro:state.registro,
    recuperarUsuario:state.recuperarUsuario,
    documentos: state.documentos
})
const mapDispatchToProps = {
    getProductos,
    getPedidos,
    getClientes,
    getProveedores,
    getDeudas,
    getCheques,
    getDocumentos,
    changeUser,
    isLoading,
    recuperandoUsuario
}
export default connect(mapStateToProps, mapDispatchToProps)(Layout)