import React, { Component } from "react"
import { Link } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import FichaProovedor from "../components/FichaProovedor"
import "./Styles/ProovedoresList.css"
import { connect } from "react-redux"
import { changeProveedor } from "../actions/index"
import { confirmAlert } from 'react-confirm-alert'; // Import
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus} from '@fortawesome/free-solid-svg-icons'

class ProovedoresList extends Component {
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        console.log(arrayClientes)
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.parentNode.style.display = "none";
            else
                f.parentNode.parentNode.style.display = "block";
        });
    }
    returnBack = () => {
        this.props.changeProveedor(undefined)
    }
    alertNuevoProveedor = () =>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nuevo Proveedor</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" class="form-control" id='nombreNuevoProveedor' placeholder="Nombre del Proveedor"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.nuevoProveedor()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
          });
    }
    nuevoProveedor = async () =>{
        const nombre = document.getElementById("nombreNuevoProveedor").value
        if(nombre){
            firebase.database().ref().child(this.props.user.uid).child('proveedores').update({
                [nombre]:{
                    datos:{
                        deuda:0,
                        nombre:nombre,
                    }
                }
            })
        }
    }
    render() {
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.proveedor) {
                    const proveedor = this.props.proveedores[this.props.proveedor]
                    if (proveedor) {
                        return (
                            <div>
                                <FichaProovedor proovedor={proveedor} seccion="Proveedores" handleClick={e => {
                                    this.returnBack()
                                }} />
                            </div>
                        )
                    }
                    else {
                        return (
                            <div>
                                <div className="productList">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col text-center">
                                                <h1 className="title">No existe el Proveedor</h1>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Proveedor" />
                                                    </div>

                                                </div>
                                            </form>
                                        </div>
                                        <div className="col">
                                            <button type="button" className="btn btn-success" onClick={this.alertNuevoProveedor}>
                                                Agregar Nuevo Proveedor
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {this.props.proveedores?
                                            Object.values(this.props.proveedores).map(proveedor => (
                                                <div className="col-12 col-md-6 col-lg-4 text-center form-group">
                                                    <Link to={{
                                                        pathname: `/Proveedores/`,
                                                        search: `${proveedor.datos.nombre}`
                                                    }}>
                                                        <button type="button" className="btn btn-outline-light btn-cliente" onClick={() => {
                                                            this.props.changeProveedor(proveedor.datos.nombre)
                                                        }}>
                                                            {proveedor.datos.nombre}
                                                        </button>
                                                    </Link>
                                                </div>
                                            ))
                                            :
                                            <>
                                                <div className="col-12 text-center">
                                                    {console.log('hola')}
                                                    <h1 className='text-white'>Aun No Hay Proveedores!</h1>
                                                </div>
                                            </>
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    )

                }
            }
        }
        catch{
            return (
                <Login redirect="Proveedores" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    proveedores: state.proveedores,
    proveedor: state.proveedor
})
const mapDispatchToProps = {
    changeProveedor,

}
export default connect(mapStateToProps, mapDispatchToProps)(ProovedoresList)