import React, { Component, Fragment } from "react"
import { Link, Redirect } from "react-router-dom"
import withFirebaseAuth from "react-with-firebase-auth"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase/app"
import Logo from "../images/logo.png"
import "./styles/Login.css"
import {
    changeUser,
    changeRegistro,
    isLoading,
    recuperandoUsuario
} from "../actions/index"
import { connect } from "react-redux"

class Registro extends Component {
    state = {
        registro:false,
        nombre:{
            valor:undefined,
            isValid:undefined
        },
        mail:{
            valor:undefined,
            isValid:true
        },
        password:{
            valor:undefined,
            isValid:undefined
        }
    }
    async registrar() {
        if(this.state.nombre.valor && this.state.mail.valor && this.state.password.valor){
            this.props.isLoading(true)
            await firebase.auth().createUserWithEmailAndPassword(this.state.mail.valor, this.state.password.valor).then(async result => {
                result.user.updateProfile({
                    displayName:this.state.nombre.valor
                })
                await firebase.auth().signInWithEmailAndPassword(this.state.mail.valor, this.state.password.valor).then(()=>{
                    this.props.changeRegistro(false)
                })
                alert('Bienvenido')
                this.props.isLoading(false)
            })
            .catch(error => {
                this.setState({
                    mail:{
                        isValid:false
                    }
                })
            })
        }
        else{
            alert('competar formulario')
        }
    }
    updateMail(data) {
        this.setState(
            {
                mail:{
                    valor:data,
                    isValid:true
                }
            }
        )
    }
    updatePass(data) {
        this.setState(
            {
                password:{
                    valor:data,
                    isValid:true
                }
            }
        )
    }
    updateNombre(data) {
        this.setState(
            {
                nombre:{
                    valor:data,
                    isValid:true
                }
            }
        )
        if(!data){
            this.setState({
                nombre:{
                    isValid:false
                }
            })
        }
    }
    render() {
        return (
            <div>
                {this.props.user ?
                    <Redirect to={"/"} />
                    :
                    <div className="container-fluid login">
                        {this.state.loading ?
                            <div className="row">
                                {this.state.loading ? <SpinnerLoading /> : null}
                            </div>
                            :
                            <Fragment>
                                <div className="row">
                                    <div className="col-10 offset-1 text-center">
                                        <img height="125px" src={Logo} />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-10 offset-1 col-md-8 offset-md-2 col-lg-6 offset-lg-3">
                                        <form className='needs-validation' noValidate>
                                            <div className="form-row">
                                                <div className="col-12 form-group">
                                                    <label>Nombre Completo</label>
                                                    <input 
                                                        required 
                                                        autoFocus 
                                                        type="text" 
                                                        className={this.state.nombre.isValid ? "form-control is-valid" : "form-control is-invalid"}
                                                        id="inputNombre"
                                                        onChange={e => {
                                                            this.updateNombre(e.target.value)
                                                        }} 
                                                        placeholder="Nombre Completo" 
                                                    />
                                                    <div className="valid-feedback">
                                                        Looks good!
                                                    </div>
                                                    <div className="invalid-feedback">
                                                        Ingresa un Nombre de Usuario
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col-12">
                                                    <label>Email</label>
                                                    <input 
                                                        required 
                                                        value={this.state.mail.valor} 
                                                        type="email" 
                                                        onChange={e => {
                                                            this.updateMail(e.target.value)
                                                        }} 
                                                        className={this.state.mail.isValid ? "form-control" : "form-control is-invalid"}
                                                        id="inputEmail" 
                                                        placeholder="Email" 
                                                    />
                                                    <div className="invalid-feedback">
                                                        El Correo Electronico ya esta registrado
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col-12">
                                                    <label>Password</label>
                                                    <input 
                                                        required 
                                                        value={this.state.password.valor} 
                                                        type="password" 
                                                        onChange={e => {
                                                            this.updatePass(e.target.value)
                                                        }} 
                                                        className={this.state.password.isValid ? "form-control is-valid" : "form-control is-invalid"}
                                                        id="inputPassword" 
                                                        placeholder="Password" 
                                                    />
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col-12 text-center">
                                                    <Link to="/Productos">
                                                        <button 
                                                            type="submit" 
                                                            onClick={e => {
                                                                e.preventDefault()
                                                                this.registrar()
                                                            }} 
                                                            className="btn btn-success">Registrarse Ahora!
                                                        </button>
                                                    </Link>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </Fragment>
                        }
                    </div>
                }
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        user: state.user,
        loading: state.loading,
    }
}
const mapDispatchToProps = {
    changeUser,
    changeRegistro,
    isLoading,
    recuperandoUsuario
}
export default connect(mapStateToProps, mapDispatchToProps)(Registro)