import React, { Component, Fragment } from "react"
import "./styles/ficha.css"
import * as firebase from "firebase"

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
class Item extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    state = {
        infoExtra: undefined
    }
    componentDidMount() {
    }
    
    async editarInfo(propiedad) {
        const nuevaInfo = prompt("Ingresar Nueva Informacion")
        if (nuevaInfo) {
            await firebase.database().ref().child(this.props.seccion + "2").child(this.props.cliente).child("datos").update(
                {
                    [propiedad]: nuevaInfo
                }
            )
        }
        else {
            await firebase.database().ref().child(this.props.seccion + "2").child(this.props.cliente).child("datos").child(propiedad).remove()
        }
    }
    async agregarInfoArray(propiedad, array) {
        let newArr = array
        let newInfo = prompt("Introducir nueva informacion")
        if (newInfo) {
            newArr.push(newInfo)
            await firebase.database().ref().child(this.props.seccion + "2").child(this.props.cliente).child("datos").update(
                {
                    [propiedad]: newArr
                }
            )
        }
    }
    async eliminarInfoArray(propiedad, array, infoDelete) {
        confirmAlert({
            title: 'Eliminar Informacion?',
            buttons: [
                {
                    label: 'Si,eliminar',
                    onClick: async () => {
                        let newArr = array
                        newArr.splice(newArr.indexOf(infoDelete), 1)
                        await firebase.database().ref().child(this.props.seccion + "2").child(this.props.cliente).child("datos").update(
                            {
                                [propiedad]: newArr
                            }
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        if (this.props.name === "nombre") {
            return (null)
        }
        else {
            if (this.props.name === "telefono" || this.props.name === "direccion" || this.props.name === "infoExtra") {
                {
                    return (
                        (this.props.name === "infoExtra" ?
                            (this.props.edit ?
                                <div className="container color-black p-2">
                                    <div className="row">
                                        <div className="col-12">
                                            <h4><small>Info Extra</small></h4>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.data).map(key => {
                                            return (
                                                <div className="col-12 text-center">
                                                    <h4 className="dataItem" onClick={e => {
                                                        this.eliminarInfoArray(this.props.name, this.props.data, this.props.data[key]).then(
                                                            this.props.editarCliente
                                                        )
                                                    }}>{this.props.data[key]}</h4>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                                :
                                <div className="container color-black p-2">
                                    <div className="row">
                                        <div className="col-12">
                                            <h4><small>Info Extra</small></h4>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.data).map(key => {
                                            return (
                                                <div className="col-12 text-center">
                                                    <h4>{this.props.data[key]}</h4>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                            )
                            :
                            (this.props.edit ?
                                <div className="container color-white p-2">
                                    <div className="row">
                                        <div class="col-12">
                                            <h3 className="nombreItem" onClick={e => {
                                                this.agregarInfoArray(this.props.name, this.props.data).then(
                                                    this.props.editarCliente()
                                                )
                                            }}> <small>{this.props.name}</small> </h3>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.data).map(key => {
                                            return (
                                                <div className="col-12 text-center">
                                                    <h3 className="dataItem" onClick={e => {
                                                        this.eliminarInfoArray(this.props.name, this.props.data, this.props.data[key]).then(
                                                            this.props.editarCliente()
                                                        )
                                                    }}>{this.props.data[key]}</h3>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                                :
                                <div className="container color-white p-2">
                                    <div className="row">
                                        <div class="col-12">
                                            <h3><small>{this.props.name}</small></h3>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.data).map(key => {
                                            return (
                                                <div className="col-12 text-center">
                                                    <h3>{this.props.data[key]}</h3>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                            )

                        )
                    )
                }
            }
            else {
                return (
                    this.props.edit ?
                        <div className="container color-white ficha p-2">
                            {this.props.name === "deuda" ?
                                <h3><small>{this.props.name}</small></h3>
                                :
                                <h3 className="nombreItem" onClick={e => {
                                    this.editarInfo(this.props.name).then(
                                        this.props.editarCliente()
                                    )
                                }}> <small>{this.props.name}</small> </h3>
                            }
                            <h3>{this.props.data}</h3>
                        </div>
                        :
                        <div className="container color-white ficha  p-2">
                            <h3 className=""><small>{this.props.name}</small></h3>
                            {this.props.name === "deuda" ?
                                (this.props.data === 0 ?
                                    <h3 className="itemDeuda " >{this.props.data}</h3>
                                    :
                                    <h3 className="">$ {this.props.data}</h3>)
                                :
                                <h3 className="">{this.props.data}</h3>
                            }
                        </div>
                )
            }
        }
    }
}
const mapStateToProps = state=>({
    user:state.user
})
export default connect(mapStateToProps,null)(Item)