import React, { Component } from "react"
import { Link } from "react-router-dom"
import PedidoItem from "./PedidoItem"
import BotoneraPedido from "./BotoneraPedido"
import "./styles/Pedido.css"
import * as firebase from "firebase"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
import {changeCliente} from '../actions/index'
class Pedido extends Component {
    state = {
        pedido: [],

    }
    async restarProducto(producto, nuevaCantidad) {
        console.log(producto, nuevaCantidad)
        const productoRef = await firebase.database().ref().child(this.props.user.uid).child('productos').child(producto)
        productoRef.update(
            {
                cantidad: nuevaCantidad
            }
        )

    }
    async descontarPedido(pedido) {
        Object.values(pedido).map(async articulo => {
            this.restarProducto(articulo.nombre, this.props.productos[articulo.nombre].cantidad - parseInt(articulo.cantidad))
        })
    }
    render() {
        return (
            <div className="pedido bg-dark p-4">
                <div className="container">
                    <div className="row text-center">
                        <div className="col-12 mb-2">
                            <Link to={{
                                pathname: `/clientes/`,
                                search: `${this.props.cliente}`
                            }}>
                                <h4 onClick={
                                    this.props.changeCliente(this.props.cliente)
                                }><span className="badge-pill badge-dark p-2">{this.props.cliente}</span></h4>
                            </Link>
                        </div>
                        <div className="col-12">
                            {Object.keys(this.props.pedido.articulos).map(articulo => (
                                <PedidoItem nombre={this.props.pedido.articulos[articulo].nombre} cantidad={this.props.pedido.articulos[articulo].cantidad} />
                            ))}
                        </div>
                    </div>
                    <BotoneraPedido
                        precio={this.props.pedido.precio}
                        enviarPedido={async e => {
                            confirmAlert({
                                title: 'Enviar Pedido?',
                                buttons: [
                                    {
                                        label: 'Enviar',
                                        onClick: async () => {
                                            await this.descontarPedido(this.props.pedido.articulos)
                                            this.props.enviarPedido(this.props.id, this.props.pedido)
                                        }
                                    },
                                    {
                                        label: 'Cancelar'
                                    }
                                ]
                            });

                        }}
                        eliminarPedido={e => {
                            confirmAlert({
                                title: 'Eliminar Pedido?',
                                buttons: [
                                    {
                                        label: 'Eliminar',
                                        onClick: async () => {
                                            this.props.eliminarPedido(this.props.id)
                                        }
                                    },
                                    {
                                        label: 'Cancelar'
                                    }
                                ]
                            });
                        }} />
                </div>
            </div>
        )
    }
}
const mapDispatchToProps={
    changeCliente
}
const mapStateToProps=state=>({
    user:state.user,
    productos:state.productos
})
export default connect(mapStateToProps,mapDispatchToProps)(Pedido)