import React, { Component } from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import NavDropdown from "react-bootstrap/NavDropdown"
import { Link } from "react-router-dom"
import logo from "../images/logo.png"
import "./styles/Navbar.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
class NavBar extends Component {
    confirmLogOut() {
        confirmAlert({
            title: 'Cerrar Sesin?',
            buttons: [
                {
                    label: 'Si, Cerrar',
                    onClick: async () => {
                        this.props.handleClick()
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        return (
            <>
                {this.props.user ?
                    <Navbar collapseOnSelect bg="dark" expand="lg" variant="dark" sticky="top">
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Brand>
                            <div className='ml-4'>
                                <div className="row">
                                    <div className="col text-center">
                                        <h6 className='text-white'>{this.props.user.displayName}</h6>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col text-center">
                                        <button type="button" className="btn btn-outline-danger" onClick={e => {
                                            this.confirmLogOut()
                                        }}>Log Out</button>
                                    </div>
                                </div>
                            </div>
                        </Navbar.Brand>
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto ml-auto">
                                <Nav.Link>
                                    <Link className="nav-link" to="/Productos">Productos</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Pedidos">Pedidos</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Clientes">Clientes</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Proveedores">Proveedores</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Deudas">Deudas</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Cheques">Cheques</Link>
                                </Nav.Link>
                            </Nav>
                        </Navbar.Collapse>
                        <Nav.Link>
                        </Nav.Link>
                            <Link className="nav-link p-2 btn btn-success" to="/NuevaVenta" id="buttonNuevoPedido">Nueva Venta</Link>
                        <Nav.Link>
                            <Link className="nav-link p-2 btn btn-outline-success" to="/NuevoRecibo" id="buttonNuevoRecibo">Nuevo Recibo</Link>
                        </Nav.Link>
                    </Navbar>
                    :
                    null
                }
            </>
        )
    }
}
const mapStateToProps=state=>({
    user:state.user
})
export default connect(mapStateToProps,null)(NavBar)