import React, { Component } from "react"
import { Link, Redirect } from "react-router-dom"
import Ficha from "./Ficha"
import FichaPedidos from "./FichaPedidos"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faTrash, faPlus,faMinus, faEdit } from '@fortawesome/free-solid-svg-icons'
import * as firebase from "firebase"
import "./styles/ficha.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
class FichaCliente extends Component {
    state = {
        edit: false,
        redirect: false
    }
    agregarArticulo(articulo, accion) {
        const arr = []
        arr.push({
            nombre: articulo,
            cantidad: accion
        })
        this.setState({
            pedido: arr
        })
    }

    eliminarCliente = () => {
        confirmAlert({
            title: 'Eliminar Cliente?',
            buttons: [
                {
                    label: 'Eliminar',
                    onClick: async () => {
                        console.log(this.props.seccion)
                        await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.proovedor.datos.nombre).remove().then(
                            this.props.handleClick()
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    };
    async agregarInfoExtra() {
        let arrayInfo
        if (this.props.proovedor.datos.infoExtra) {
            arrayInfo = this.props.proovedor.datos.infoExtra
        }
        else {
            arrayInfo = []
        }
        arrayInfo.push(prompt("Ingresar Informacion Extra"))
        await firebase.database().ref().child(this.props.user.uid).child("clientes").child(this.props.proovedor.datos.nombre).child("datos").update(
            {
                infoExtra: arrayInfo
            }
        )


    }
    alertEditarCliente=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Editar Informacion</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="number" className="form-control" id='inputNuevoCa' placeholder="Inresar Cantidad"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.sumarDeuda()
                                            onClose();
                                        }}
                                    >
                                    <FontAwesomeIcon icon={faPlus}  />
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-danger'
                                        onClick={() => {
                                            this.restarDeuda()
                                            onClose();
                                        }}
                                    >
                                        <FontAwesomeIcon icon={faMinus}  />
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    editarCliente() {
        this.setState({
            edit: !this.state.edit
        })

    }
    render() {
        return (
            <div>
                <div className="productList">
                    <div className="container-fluid">
                        <div className="row nav">
                            <div className="col-5 offset-1 text-left">
                                <Link to={{
                                    pathname: "/clientes"
                                }}>
                                    <h1> <FontAwesomeIcon className="backIcon" icon={faArrowLeft} onClick={e => {
                                        this.props.handleClick()
                                        this.editarCliente()
                                    }} />
                                    </h1>
                                </Link>
                            </div>
                            <div className="col-3 offset-3 danger-zone">
                                <h1> <FontAwesomeIcon className="backIcon" icon={faEdit} onClick={e => {
                                    this.editarCliente()
                                }} /></h1>
                                <h1> <FontAwesomeIcon className="backIcon" icon={faTrash} onClick={e => {
                                    this.eliminarCliente()
                                }} /></h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center form-group">
                                <Ficha
                                    editarCliente={e => {
                                        this.editarCliente()
                                    }}
                                    edit={this.state.edit}
                                    seccion={'clientes'}
                                    cliente={this.props.proovedor}
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h2 className="name">Lista de Pedidos</h2>
                            </div>
                            <div className="col-12 text-center form-group pt-2">
                                <FichaPedidos data={this.props.proovedor.pedidos} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state =>({
    user:state.user,
})
export default connect(mapStateToProps,null)(FichaCliente)