import React, { Component } from "react"
import { Link, Redirect } from "react-router-dom"
import NavBar from "./NavBar"
import Ficha from "./Ficha"
import FichaPedidos from "./FichaPedidos"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faTrash, faEdit, faPlus } from '@fortawesome/free-solid-svg-icons'
import * as firebase from "firebase"
import "./styles/ficha.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
class FichaProovedor extends Component {
    state = {
        edit: false
    }
    agregarArticulo(articulo, accion) {
        const arr = []
        arr.push({
            nombre: articulo,
            cantidad: accion
        })
        this.setState({
            pedido: arr
        })
    }
    editarCliente() {
        if (this.state.edit) {
            this.setState({
                edit: false
            })
        }
        else {
            this.setState({
                edit: true
            })
        }
    }
    eliminarCliente = () => {
        confirmAlert({
            title: 'Eliminar Cliente?',
            buttons: [
                {
                    label: 'Eliminar',
                    onClick: async () => {
                        await firebase.database().ref().child(this.props.user.uid).child('proveedores').child(this.props.proovedor.datos.nombre).remove().then(
                            this.props.handleClick()
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    };
    render() {
        return (
            <div>
                <div className="productList">
                    <div className="container-fluid">
                        <div className="row nav">
                            <div className="col-5 offset-1 text-left">
                                <Link to={{
                                    pathname: `/${this.props.seccion}`
                                }}>
                                    <h1> <FontAwesomeIcon className="backIcon" icon={faArrowLeft} onClick={this.props.handleClick} />
                                    </h1>
                                </Link>
                            </div>
                            <div className="col-3 offset-3 danger-zone">
                                <h1> <FontAwesomeIcon className="backIcon" icon={faEdit} onClick={e => {
                                    this.editarCliente()
                                }} /></h1>
                                <h1> <FontAwesomeIcon className="backIcon" icon={faTrash} onClick={e => {
                                    this.eliminarCliente()
                                }} /></h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center form-group">
                                <Ficha 
                                    editarCliente={e => {
                                        this.editarCliente()
                                    }} 
                                    edit={this.state.edit} 
                                    seccion={'proveedores'} 
                                    cliente={this.props.proovedor} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h2 className="name">Lista de Pedidos</h2>
                            </div>
                            <div className="col-12 text-center form-group pt-2">
                                <FichaPedidos seccion='proveedores' data={this.props.proovedor.pedidos} />
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state=>({
    user:state.user
})
export default connect(mapStateToProps,null)(FichaProovedor)